// app/login/page.tsx

"use client";

import { useEffect } from "react";
import { useRouter } from "next/navigation";
import { GoogleAuthProvider, signInWithPopup } from "firebase/auth";
import { auth } from "@/lib/firebase";
import { useAuth } from "@/context/AuthContext";
import Image from "next/image";
import { CheckCircleIcon } from "@heroicons/react/24/solid";

export default function LoginPage() {
  const { user, loading, isFirstLogin } = useAuth();
  const router = useRouter();

  // [기존 로직] (수정 없음)
  useEffect(() => {
    if (loading) return;
    if (user) {
      if (isFirstLogin === true) {
        router.push("/profile/setup");
      } else if (isFirstLogin === false) {
        router.push("/dashboard");
      }
    }
  }, [user, loading, isFirstLogin, router]);

  // [기존 로직] (수정 없음)
  const handleGoogleLogin = async () => {
    const provider = new GoogleAuthProvider();
    try {
      await signInWithPopup(auth, provider);
    } catch (error) {
      console.error("로그인 중 에러 발생:", error);
    }
  };

  // [기존 로직] (수정 없음)
  if (loading || user) {
    return (
      <div className="flex min-h-screen items-center justify-center bg-gray-50">
        로그인 정보 확인 중...
      </div>
    );
  }

  // [수정] 단일 컬럼 + 브랜딩 요소가 포함된 레이아웃으로 변경
  return (
    <div className="flex min-h-screen flex-col justify-center bg-gray-50 px-6 py-12">
      {/* 모든 컨텐츠를 감싸는 중앙 정렬 컨테이너 */}
      <div className="mx-auto w-full max-w-lg">
        
        {/* 1. 로고 및 헤더 */}
        <div className="mb-8 text-center">
          <h1 className="text-4xl font-bold text-gray-900">RuleMakers</h1>
          <p className="mt-2 text-lg text-gray-600">
            강사님을 위한 컨텐츠 요청 시스템
          </p>
        </div>

        {/* 2. 로그인 카드 */}
        <div className="rounded-lg bg-white p-8 shadow-md sm:p-10">
          <div className="mb-6 text-center">
            <h2 className="text-2xl font-bold text-gray-900">시작하기</h2>
            <p className="mt-2 text-gray-500">
              Google 계정으로 간편하게 시작하세요.
            </p>
          </div>
          <button
            onClick={handleGoogleLogin}
            className="flex w-full items-center justify-center gap-3 rounded-lg border border-gray-300 bg-white px-4 py-3 font-medium text-gray-700 shadow-sm transition-colors hover:bg-gray-100"
          >
            <Image
              src="/google-logo.svg"
              alt="Google"
              width={20}
              height={20}
            />
            <span>Google 계정으로 로그인</span>
          </button>
        </div>

        {/* 3. 브랜딩 섹션 (카드 바깥) */}
        <div className="mt-12">
          <p className="text-center text-xl text-gray-800">
            {/* 요청하신 문구 적용 */}
            고객님을 위한 단 하나의 프리미엄 컨텐츠,
            <br />
            지금 바로 경험해보세요.
          </p>

          {/* 기능 목록 (라이트 모드에 맞게 수정) */}
          <div className="mt-10 space-y-6 text-left">
            <FeatureItem
              icon={<CheckCircleIcon className="h-6 w-6 text-blue-600" />}
              title="학교별 맞춤형 컨텐츠"
              description="최신 기출 경향과 데이터를 분석하여 맞춤형 문항을 제작합니다."
            />
            <FeatureItem
              icon={<CheckCircleIcon className="h-6 w-6 text-blue-600" />}
              title="신속한 제작 및 검수"
              description="요청 후 3일 이내에 검수까지 완료된 최종본을 제공합니다."
            />
            <FeatureItem
              icon={<CheckCircleIcon className="h-6 w-6 text-blue-600" />}
              title="강의 전용 대시보드"
              description="모든 요청 내역과 제작된 컨텐츠를 한곳에서 관리합니다."
            />
          </div>
        </div>
      </div>
    </div>
  );
}

// [수정] 헬퍼 컴포넌트 (라이트 모드에 맞게 텍스트 색상 변경)
function FeatureItem({
  icon,
  title,
  description,
}: {
  icon: React.ReactNode;
  title: string;
  description: string;
}) {
  return (
    // 아이콘과 텍스트가 위로 정렬되도록 items-start 추가
    <div className="flex items-start gap-4">
      <div className="flex-shrink-0">{icon}</div>
      <div>
        <h3 className="text-lg font-semibold text-gray-900">{title}</h3>
        <p className="text-gray-600">{description}</p>
      </div>
    </div>
  );
}